<h1 id="linux中的防火墙" tabindex="-1"><a class="header-anchor" href="#linux中的防火墙" aria-hidden="true">#</a> Linux中的防火墙</h1>
<h2 id="firewalld" tabindex="-1"><a class="header-anchor" href="#firewalld" aria-hidden="true">#</a> firewalld</h2>
<h3 id="基本操作命令" tabindex="-1"><a class="header-anchor" href="#基本操作命令" aria-hidden="true">#</a> 基本操作命令</h3>
<ul>
<li>启动防火墙</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl start firewalld.service
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>关闭防火墙</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl stop firewalld.service
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>查看防火墙状态</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl status firewalld.service
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>开机启用防火墙</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl <span class="token builtin class-name">enable</span> firewalld.service
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>开机禁用防火墙</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl disable firewalld.service
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="开放端口配置" tabindex="-1"><a class="header-anchor" href="#开放端口配置" aria-hidden="true">#</a> 开放端口配置</h3>
<ul>
<li>添加80端口</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --zone<span class="token operator">=</span>public --add-port<span class="token operator">=</span><span class="token number">80</span>/tcp --permanent
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>添加8080端口</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --zone<span class="token operator">=</span>public --add-port<span class="token operator">=</span><span class="token number">8080</span>/tcp --permanent 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>查看开放的端口</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --list-all
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>重载防火墙配置</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --reload
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>查看所有打开的端口</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --zone<span class="token operator">=</span>public --list-ports
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="其他辅助命令" tabindex="-1"><a class="header-anchor" href="#其他辅助命令" aria-hidden="true">#</a> 其他辅助命令</h3>
<ul>
<li>查看版本</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --version
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>查看帮助</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>firewall-cmd --help
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>